.wcb-form__wrap [type=checkbox]:checked {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");
}
.wcb-form__wrap [type=checkbox]:checked::before {
  display: none;
}
.wcb-form__wrap [type=radio]:checked {
  background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='8' cy='8' r='3'/%3e%3c/svg%3e");
}
.wcb-form__wrap [type=radio]:checked::before {
  display: none;
}
.wcb-form__wrap .wcb-form__label {
  display: block;
  color: #374151;
}
.wcb-form__wrap .wcb-form__label.required::after {
  color: #f00;
  content: " *";
}
.wcb-form__wrap .wcb-checkbox__option-label {
  color: #374151;
}
.wcb-form__wrap .wcb-checkbox__option-label.required::after {
  color: #f00;
  content: " *";
}

.wcb-form__btn-submit {
  @apply inline-flex items-center justify-center px-4 py-2 text-base;
}

.wcb-form__successMessageText,
.wcb-form__errorMessageText {
  @apply p-5;
}